<template>
	<auth-template
    title="Faça o seu cadastro" 
    :alert="alertInfo"
    :isBarber="isBarber"
  >
    <form @submit.prevent="cadastrar">
      <v-text-field
        id="name"
        label="Nome"
        v-model="name"
        v-on:keyup.enter="focusToNext('email')"
      />
      
      <v-text-field
        ref="email"
        v-model="email"
        label="E-mail"
        :disabled="emailDisabled"
        :rules="emailRules"
        v-on:keyup.enter="focusToNext('password')" 
      />
      
      <v-text-field
        ref="password"
        :type="showPassword ? 'text' : 'password'"
        v-model="password"
        :rules="passwordRules"
        label="Senha"
        :append-icon="showPassword ? 'visibility_off' : 'remove_red_eye'"
        @click:append="showPassword = !showPassword"
        v-on:keyup.enter="focusToNext('confirmPassword')" 
      />
      
      <v-text-field
        ref="confirmPassword"
        :type="showConfirmPassword ? 'text' : 'password'"
        v-model="confirmPassword"
        :rules="passwordRules"
        label="Confirmar Senha"
        :append-icon="showConfirmPassword ? 'visibility_off' : 'remove_red_eye'"
        @click:append="showConfirmPassword = !showConfirmPassword"
        v-on:keyup.enter="enterClick" 
      />
      
      <div class="text-center mt-4">
        <btn-confirm
          text="Cadastrar"
          :disabled="btnDisabled"
          :loading="progressLoader"
          @clicked="cadastrar"
        />
      </div>

      <div class="text-center mt-4">
        <span class="d-block font-weight-light">ou</span>
        <btn-text
          text="Voltar"
          class="mt-2"
          @clicked="voltar"
          dark
        />
      </div>
    </form>
  </auth-template>
</template>

<script>
import AuthTemplate from './AuthTemplate'
import BtnConfirm from '@/components/globais/buttons/confirm/BtnConfirm'
import BtnText from '@/components/globais/buttons/text/BtnText'

import FormMixin from '@/mixins/FormMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'Register',
  components: { AuthTemplate, BtnConfirm, BtnText },
  mixins: [ FormMixin, NavigationMixin, ValidationMixin ],
  props: { 
    alertInfo: { default: null },
    isBarber: { default: false },
    progressLoader: { default: false },
    token: { default: '' }
  },
  data: () => ({
    showPassword: false,
    showConfirmPassword: false,
    name: '',
    email: '',
    password: '',
    confirmPassword: ''
  }),
  watch: {
    token (val) {
      if (val) {
        let jwt = this.parseJwt(val)
        this.email = jwt.usuario.barber_mail
      }
    }
  },
  computed: {
    emailDisabled () {
      return this.token ? true : false
    },
    btnDisabled () {
      if (!this.isNameValid(this.name)) return true
      else if (!this.isEmailValid(this.email)) return true
      else if (!this.isPasswordValid(this.password)) return true
      else if (!this.isPasswordValid(this.confirmPassword)) return true
      else if (this.confirmPassword !== this.password) return true
      return false
    }
  },
  mounted () {
    document.querySelector('#name').focus()
  },
  methods: {
    enterClick () {
      if (!this.btnDisabled) this.cadastrar()
      else this.showSnackbarErro('Preencha todos os dados corretamente!')
    },
    cadastrar () {
      let data = {
        name: this.name,
        email: this.email,
        password: this.password
      }
      this.$emit('register', data)
    },
    parseJwt (token) {
      let base64Url = token.split('.')[1]
      let base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/')
      /*let jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2)
      }).join(''))*/
      let jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2)
      }).join(''))
      return JSON.parse(jsonPayload)
    },
    voltar () {
      if (this.isBarber) this.goTo('login-barber')
      else this.goTo('login')
    }
  }
}
</script>